.App {
  text-align: center;
  min-height: 100vh;
  background-color: rgb(19, 2, 2);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.center{
  position: absolute;
  width: 72%;
  height: 69%;
  background-color: rgb(22, 7, 10);
  border-radius: 1.5rem;
}

.circle1{
  width: 15%;
  height: 19%;
  background-color: rgba(110, 54, 54, 0.5);
  position: absolute;
  top: 14%;
  right:13%;
  border-radius: 0% 11% 0% 0%;
  box-shadow: 0px 0px 10px 1px black;
}
.circle2{
  width: 15%;
  height: 19%;
  background-color: rgba(110, 54, 54, 0.5);
  position: absolute;
  bottom: 14%;
  left:13%;
  border-radius: 0% 0% 0% 11%;
  box-shadow: 0px 0px 10px 1px black;
}

.content{
  position: relative;
  top:5px
}

.filler{
  z-index: 2;
  position: relative;
}

.username{
  width: 100%;
  width: 50%;
}

form{
  display: flex;
    flex-direction: column;
    align-items: flex-end;
}
b{
  margin:10px;
}

#basicUsername,#basicPassword,#basicWebsite,#basicCode{
 
  width: 24rem;
  border-radius: 0.2rem;
  border-color: transparent;
  text-align: center;
  background-color: rgb(41, 38, 38);
  font-size: 1rem;
  padding: 2px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color:white;
}
