div.card{
  background-color: transparent;
  color:wheat;
  margin-top: 2%;
  display: flex;
  overflow-wrap: anywhere;
  position: absolute;
  bottom: 5%;
}
div.manage{
  position: absolute;
  top:5%;
  right: 5%;
  padding: 10px;
  background-color: rgb(54, 22, 22);
  border-radius: 4px;
  user-select: none;
  color: white;
}

div.manage:hover{
  box-shadow: 0px 0px 5px 5px black;
}