.cardN{
    min-height: 100vh;
    background-color:rgb(19, 2, 2);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Container{
    margin: 4%;
    height:32em;
    background-color: rgb(34, 12, 12);
    display: flex;
    position: relative;
    border-radius: 8px 8px 8px 8px;
    box-shadow: 0px 0px 100px black;

}
.Container2{
    flex:1;
    background-color: rgb(61, 45, 45);
    padding: 10px;
    border-radius: 8px 0px 0px 8px;

}
.Container3{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin: 10px;
    overflow-wrap: anywhere;
    color: white;
    text-align: center;
    border-radius: 0px 0px 8px 50px;
}
textarea.rxr.form-control{
    height: 140%;
    background-color: black;
    border-color: transparent;
    color:white;
    font-weight: bold;
}
h3{
    font-weight: bolder;
    text-shadow: 2px 2px 8px black ;
}
.key>h3{
    padding-top: 43px;
}

textarea.xrx.form-control{
    background-color: black;
    border-color: transparent;
    height: 150%;
}
.buttonClass{
    margin-top:20px ;
}
.passwordDisplay{
    margin-top: 10%;
    height: 20%;
    width: auto;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;

}
.password{
    text-align: center;
    background-color: crimson !important;
    position: relative !important;
    padding:4%;
    width: 50%;
    height: 60%;
    display: flex;
    justify-content: center;
}
